<template>
  <div class="row">
    <div class="col-12 mt-3">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 ml-2">Add customer</h5>
          </div>
          <div class="flex-align-center flex-end">
            <el-button
              @click="$router.go(-1)"
              type="primary"
              v-ripple.400
              class="flat"
              color="rgb(255,255,255)"
            >
              Discard
            </el-button>
            <el-button
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Post"
              type="primary"
            >
              Save
            </el-button>
          </div>
        </div>

        <div class="col-lg-8 pr-lg-0">
          <div class="box">
            <p class="mt-0 mb-2 text-bold">Details</p>
            <div class="row">
              <div class="col-12">
                <label for="name" class="custom-label">Name</label>
                <el-input name="name" class="w-100" v-model="customer.name" />
              </div>
              <div class="col-6 mt-3">
                <label for="email" class="custom-label">Email</label>
                <el-input name="email" class="w-100" v-model="customer.email" />
              </div>
              <div class="col-6 mt-3">
                <label for="phone" class="custom-label">Phone</label>
                <el-input
                  name="phone"
                  type="number"
                  class="w-100"
                  v-model="customer.phone"
                />
              </div>
            </div>
          </div>

          <div class="box mt-3">
            <p class="mt-0 mb-2 text-bold">Address</p>
            <div class="row">
              <div class="col-12">
                <label for="" class="custom-label md-text">State & city</label>
                <el-cascader
                  v-model="address"
                  :options="options"
                  @change="handleChangeAddress"
                ></el-cascader>
              </div>
              <div class="col-12 mt-3">
                <label for="Street address" class="custom-label"
                  >Street address</label
                >
                <el-input
                  name="Street address"
                  class="w-100"
                  v-model="customer.address.address"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="col-lg-4 mt-3 mt-lg-0">
          <div class="box">
            <p class="mt-0 mb-2 text-bold">Organization</p>

            <label class="custom-label mb-0">Note</label>
            <el-input type="textarea" v-model="customer.note"></el-input>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import address from "../../../core/constant/address";

export default {
  data() {
    return {
      saving: false,
      address: [],
      options: address,
      customer: {
        address: {
          state: "",
          city: "",
          map: "",
          address: "",
        },
        sales: [],
        name: "",
        email: "",
        phone: "",
        note: "",
      },
      loading: null,
      startEditing: false,
      orders: [],
      search: "",
      page: 1,
      max: 5,
      total: 0,
      avg: 0,
    };
  },
  watch: {
    customer: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
  },
  components: {},
  mounted() {
    this.$store.commit("setIsReady", true);
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    handleChangeAddress(address) {
      this.customer.address.state = address[0];
      this.customer.address.city = address[1];
    },
    Post() {
      this.saving = true;
      this.customer.store = this.store.id;
      this.$axios
        .post("customer/", { data: this.customer })
        .then(() => {
          this.startEditing = false;
          this.saving = false;
          this.$noti.display({
            message: "Cool! customer has been successfully added.",
            title: "Success",
            status: "normal",
            color: "success",
          });
          this.$router.push({ name: "Customers" });
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
          this.saving = false;
        });
    },
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
};
</script>
<style >
</style>
